import { Routes } from '@angular/router';

import { TaskboardComponent } from './taskboard/taskboard.component';
import { FullcalendarComponent } from './fullcalendar/fullcalendar.component';
import { FormMantenedorComponent } from './form-composer/pages/mantenedor/create-modify.component';
import { ViewFormsComponent } from './form-composer/pages/monitor/view-forms.component';
import { DisplayComponent } from './form-composer/pages/display/display.component';
import { LoginComponent } from './form-composer/pages/login/login.component';
import { SessionGuard } from './form-composer/services/session.guard';

export const AppsRoutes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'form-composer',
        canActivate: [SessionGuard],
        component: FormMantenedorComponent,
        data: {
          title: 'Forms Editor',
          urls: [{ title: 'Forms Editor', url: '/apps/form-composer' }, { title: 'View forms', url: '/apps/view-forms' }, {}]
        }
      },
      {
        path: 'view-forms',
        canActivate: [SessionGuard],
        component: ViewFormsComponent,
        data: {
          title: 'View forms',
          urls: [{ title: 'Forms Editor', url: '/apps/form-composer' }, { title: 'View forms', url: '/apps/view-forms' }, {}]
        }
      },
      {
        path: 'display/:caseName',
        canActivate: [SessionGuard],
        component: DisplayComponent,
        data: {
          title: 'display',
          urls: [{ title: 'Forms Editor', url: '/apps/form-composer' }, { title: 'View forms', url: '/apps/view-forms' }, {}]
        }
      },
      {
        path: 'login',
        component: LoginComponent,
        data: {
          title: 'login',
          urls: [{ title: 'Forms Editor', url: '/apps/form-composer' }, { title: 'View forms', url: '/apps/view-forms' }, {}]
        }
      },
      {
        path: 'taskboard',
        canActivate: [SessionGuard],
        component: TaskboardComponent,
        data: {
          title: 'Taskboard',
          urls: [{ title: 'Dashboard', url: '/dashboard' }, { title: 'Taskboard' }]
        }
      },
      {
        path: 'fullcalendar',
        canActivate: [SessionGuard],
        component: FullcalendarComponent,
        data: {
          title: 'Full-Calendar',
          urls: [{ title: 'Dashboard', url: '/dashboard' }, { title: 'Full-Calendar' }]
        }
      }
    ]
  }
];
