import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { CalendarModule } from 'angular-calendar';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { DragulaModule } from 'ng2-dragula/ng2-dragula';
import { QuillModule } from 'ngx-quill';

import { AppsRoutes } from './apps.routing';
import { TaskboardComponent } from './taskboard/taskboard.component';
import { FullcalendarComponent } from './fullcalendar/fullcalendar.component';
import { FormMantenedorComponent } from './form-composer/pages/mantenedor/create-modify.component';
import { ViewFormsComponent } from './form-composer/pages/monitor/view-forms.component';
import { DisplayComponent } from './form-composer/pages/display/display.component';
import { LoginComponent } from './form-composer/pages/login/login.component';

@NgModule({
  // tslint:disable-next-line:max-line-length
  imports: [
    CommonModule,
    FormsModule,
    HttpClientModule,
    NgbModule,
    CalendarModule.forRoot(),
    QuillModule.forRoot(),
    DragulaModule,
    RouterModule.forChild(AppsRoutes)],
  declarations: [
    TaskboardComponent,
    FullcalendarComponent,
    FormMantenedorComponent,
    ViewFormsComponent,
    DisplayComponent,
    LoginComponent]
})
export class AppsModule { }
