import { HttpHeaders, HttpParams } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { JwtModel } from '../models/jwt.model';
import { SesionModel } from '../models/sesion.model';
import jwt_decode from 'jwt-decode';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class SessionService {

  url = 'http://localhost:8080/';
  userToken: string;

  constructor(private http: HttpClient, private router: Router) { }



  login(sesion: any): any {
    // const params = new HttpParams()
    //   .set('usuario', sesion.usuario)
    //   .set('contrasena', sesion.contrasena);
    console.log(sesion);
    this.http.post(this.url+ "login",
    {
      'username': sesion.usuario,
      'password': sesion.contraseña
    }).subscribe(
      (res:any) =>{
        // console.log(res);
        localStorage.setItem('authorization', res.token);
        this.router.navigateByUrl("/apps/form-composer");
      }
    );
  }

  logout() {
    this.limpiarToken();
  }

  private limpiarToken() {
    localStorage.removeItem('authorization');
  }

  leerToken(): string {
    if (localStorage.getItem('authorization')) {
      this.userToken = localStorage.getItem('authorization');
      return this.userToken;
    } else {
      this.limpiarToken();
      return null;
    }

    // return jwt_decode(this.userToken) as JwtModel;
  }

  // estaAutenticado():Observable<any>{
  //   const token =  this.leerToken();
  //   // const httpOptions =  {
  //   //   headers: new HttpHeaders({
  //   //     'Content-Type':  'application/json',
  //   //     authorization: `${token}`
  //   //   })
  //   // };
  //   this.http.post<any>(this.url+ "login", null,
  //   {
  //     headers: new HttpHeaders({
  //       'Content-Type':  'application/json',
  //       authorization: `${token}`
  //     })
  //   }
  //   ).pipe(
  //     map(
  //         (response) => {
  //             return response;
  //         }
  //     )
  //   ) as Observable<any>

  // }

  estaAutenticado(): any {

    return this.http.post(
        `${this.url}login`,
        null,
        this.httpOptions()) as Observable<any>;
}

  httpOptions () {
    const token =  this.leerToken();
    console.log(token);
    return {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
      authorization: `Bearer ${token}`
    })
    }
  };

  headerToken(valor:string): HttpHeaders {
    return new HttpHeaders({
      'authorization': 'valor'
    });
  }

}
