import { SessionService } from './../../services/session.service';
import { HttpParams } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { AfterViewInit } from '@angular/core';
import { Component } from '@angular/core';
import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';


declare var $: any;

const fbEditor = document.getElementById("build-wrap");

@Component({
  selector: 'app-main-form-composer',
  templateUrl: './create-modify.component.html',
  styleUrls: []
})

@Injectable()
export class FormMantenedorComponent implements AfterViewInit {
  ngAfterViewInit(): void {
  }

  // varables display
  viewCardFormBuilder: boolean = false;
  viewButtonsCreate: boolean = false;
  viewButtonsUpdate: boolean = false;
  displayFormularios: boolean = false;
  formCreateForms: boolean = false;
  formModifyForms: boolean = false;

  // variables otros
  formData: any;
  formNumber: number;
  formularioConstructor: any;
  getCheckbox: any;

  options: {
    headers?: HttpHeaders | { [header: string]: string | string[] },
    observe?: 'body' | 'events' | 'response',
    params?: HttpParams | { [param: string]: string | string[] },
    reportProgress?: boolean,
    responseType?: 'arraybuffer' | 'blob' | 'json' | 'text',
    withCredentials?: boolean,
  }

  constructor(private http: HttpClient, private sessionService: SessionService) { }

  // displays de elementos en el HTML

  mostrarCartaFormularios() {
    if (this.formModifyForms === false && this.formCreateForms === false) {
      this.displayFormularios = false;
    }
    else if (this.formModifyForms === true || this.formCreateForms === true) {
      this.displayFormularios = true;
    }
  }

  mostrarFormularioCrear() {
    if (this.viewCardFormBuilder === true && this.formModifyForms === true) {
      this.formModifyForms = false
      this.viewCardFormBuilder = false;
    }
    if (this.formCreateForms === false) {
      this.formCreateForms = true
    } else {
      this.formCreateForms = false;
    }
    if (this.formModifyForms === true) {
      this.formModifyForms = false
    }
    this.mostrarCartaFormularios();
  }

  mostrarFormularioEditar() {
    if (this.viewCardFormBuilder === true && this.formCreateForms === true) {
      this.formCreateForms = false;
      this.viewCardFormBuilder = false;
    }
    if (this.formModifyForms === false) {
      this.formModifyForms = true;
    } else {
      this.formModifyForms = false;
    }
    if (this.formCreateForms === true) {
      this.formCreateForms = false;
    }
    this.mostrarCartaFormularios();
  }

  mostrarFormulario(casoFormulario): any {
    let configUrl = `http://localhost:8080/checkbox/${casoFormulario}`;
    this.http.get(configUrl, this.sessionService.httpOptions()).subscribe(
      (res: any) => {
        if (this.viewCardFormBuilder === false) {
          this.viewCardFormBuilder = true;
          this.viewButtonsUpdate = true;
          setTimeout(() => {
            this.formularioConstructor = $(document.getElementById("build-wrap")).formBuilder({
              disabledActionButtons: ['data', "save", "clear"],
              disableFields: [
                'autocomplete',
                'button',
                // 'checkbox-group',
                'date',
                'file',
                'header',
                'hidden',
                // 'number',
                'paragraph',
                // 'radio-group',
                // 'select',
                'starRating',
                // 'text',
                'textarea'
              ],
              defaultFields: this.formData
            });
            console.log("cargo boton de actualizar");
          }
            , 0);
        }
      },
      (err: any) => {
        console.log("Ocurrio el siguiente error: ", err.error.text);
      });
  }

  // Conexiones con el back-end
  // Botones crear y modificar
  crearInformacion(evento) {
    this.formNumber = evento.target.seleccionCaso.value;
    if (this.formNumber > 0) {
      let informacion: any = {
        nombre: this.formNumber,
        formulario: '[]'
      };
      this.http.post('http://localhost:8080/checkbox', informacion, this.sessionService.httpOptions())
        .subscribe(
          async (res: any) => {
            this.formData = await informacion;
            await this.mostrarFormulario(this.formNumber);
            this.displayFormularios = false;
            this.formCreateForms = false;
          },
          (err) => {
            alert("Intente otro valor");
          });
    } else {
      alert('Ingrese un valor valido');
    }
  }

  modificarInformacion(evento) {
    this.formNumber = evento.target.seleccionCaso.value;
    // Verifica si existe el caso para modificar
    if (this.formNumber > 0) {
      this.http.get(`http://localhost:8080/checkbox/${this.formNumber}`, this.sessionService.httpOptions())
        .subscribe(
          async (res: any) => {
            this.formData = await (JSON.parse(res.formulario));
            await this.mostrarFormulario(this.formNumber);
            this.displayFormularios = false;
            this.formModifyForms = false;
          },
          (err) => { alert(JSON.stringify(err)) }
        );
    } else {
      alert('Ingrese un nombre de formulario valido');
    }
  }

  // Funciones compartidas
  actualizarInformacion() {
    let informacion: any = {
      nombre: this.formNumber,
      formulario: JSON.stringify(this.formularioConstructor.actions.getData())
    };
    this.http.patch(`http://localhost:8080/checkbox/${this.formNumber}`, informacion, this.sessionService.httpOptions())
      .subscribe(
        (res) => {
          alert("actualizado correctamente");
          if (this.viewCardFormBuilder === true) {
            this.formularioConstructor = '';
            this.viewCardFormBuilder = false;
          }
        },
        (err) => { console.log(err) });
  }

  obtenerInformacionFormularios() {
    this.http.get('http://localhost:8080/checkbox', this.sessionService.httpOptions())
      .subscribe((res) => { return res });
  }


}
