import { SessionService } from './../../services/session.service';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-display',
  templateUrl: './display.component.html',
  styleUrls: []
})
export class DisplayComponent implements OnInit {
  formElement: any;
  htmlElement: any[] = [];
  constructor(
    private route: ActivatedRoute,
    private http: HttpClient,
    private sessionService: SessionService
  ) { }
  async ngOnInit() {
    const caseParam = this.route.snapshot.paramMap.get('caseName');
    this.http.get(`http://localhost:8080/checkbox/${caseParam}`, this.sessionService.httpOptions())
      .subscribe((res: any) => {
        console.log(res);
        this.formElement = res;
        JSON.parse(res.formulario).map(
          (element) => {
            this.htmlElement.push(
              element
            );
            // if (element.type === 'checkbox-group') {
            //   console.log(this.htmlElement);
            //   this.htmlElement.push(
            //     element
            //   );
            // }
            // console.log(element);
            // console.log(element.type);
            // console.log(element.label);
            // if (element.values) {
            //   element.values.map(
            //     (value) => {
            //       this.htmlElement.push(
            //         value
            //       );
            //     }
            //   )
            // }
          }
        )
      });
  }
  preguntar(valor) {
    console.log(valor);
  }
}
